import dragonBones from '@mk/dragonbones';
import BaseTest from './drobge';

export default class Person extends BaseTest {
  _armatureDisplayA=null

  constructor() {
    super();
    this._resources.push(
      '/static/SwordsMan_1_ske.json',
      '/static/SwordsMan_1_tex.json',
      '/static/SwordsMan_1_tex.png',
    );
    // this._onStart();
  }

  _onStart() {
    console.log(233);

    const { factory } = dragonBones.HiloFactory;
    factory.parseDragonBonesData(this._hiloResources['/static/SwordsMan_1_ske.json']);
    factory.parseTextureAtlasData(this._hiloResources['/static/SwordsMan_1_tex.json'], this._hiloResources['/static/SwordsMan_1_tex.png']);
    //
    this._armatureDisplayA = factory.buildArmatureDisplay('Swordsman');

    const sourceArmatureData = factory.getArmatureData('Swordsman');

    this.addChild(this._armatureDisplayA);

    this._armatureDisplayA.scaleX = 0.3;
    this._armatureDisplayA.scaleY = 0.3;
    this._armatureDisplayA.animation.timeScale = 0.5;

    this._armatureDisplayA.x = this.stageWidth * 0.5 - 700;
    this._armatureDisplayA.y = this.stageHeight * 0.5 + 220.0;

    this._armatureDisplayA.animation.play('walk');
    this.on((Hilo.event).POINTER_START, () => {
        this._replaceAnimation();
    }, false);

    // this._initBackground();
  }

 _replaceAnimation() {
    let animationName = this._armatureDisplayA.animation.lastAnimationName;
   console.log(animationName, this._armatureDisplayA.animation, '1jjfjdsjafjdsagljak');
    if (animationName) {
        const { animationNames } = this._armatureDisplayA.animation;
        const animationIndex = (animationNames.indexOf(animationName) + 1) % animationNames.length;
        this._armatureDisplayA.animation.play(animationNames[animationIndex]);
    } else {
        this._armatureDisplayA.animation.play();
    }

    animationName = this._armatureDisplayA.animation.lastAnimationName;

    this._armatureDisplayA.animation.play(animationName);
}
}
