import dragonBones from '@mk/dragonbones';

export default class BaseTest extends Hilo.Container {
  _stage = new Hilo.Stage({
    renderType: 'webgl',
    container: document.getElementById('hilo-container'),
    width: 1836,
    height: 640,
  });

  _ticker = new Hilo.Ticker(60);

  _resources =[];

  constructor() {
    super({ background: '#666666', width: 1836, height: 640 });
    const gameContainer = document.getElementById('hilo-container');
    gameContainer.style.height = `${this.stageHeight }px`;
    gameContainer.style.width = `${this.stageWidth }px`;
    //
    this._hiloResources = {};

    this._stage.enableDOMEvent([(Hilo.event).POINTER_START, (Hilo.event).POINTER_MOVE, (Hilo.event).POINTER_END], true);
    this.addTo(this._stage, 0);
    //
    this._ticker.addTick(this);
    this._ticker.addTick(dragonBones.HiloFactory.factory.dragonBones);
    this._ticker.addTick(this._stage);
    //

    setTimeout(() => {
        this._loadResources();
    }, 10);

    // this.initBack();
  }

  initBack() {
      const bgWidth = this.stageHeight;
      const bgHeight = this.stageWidth;
      document.body.insertBefore(Hilo.createElement('div', {
        id: 'bg',
        style: {
            position: 'absolute',
            background: 'url(/static/timg (1).jpeg) no-repeat',
            backgroundSize: `${bgWidth }px, ${ bgHeight }px`,
            width: `${bgWidth }px`,
            height: `${bgHeight }px`,
        },
      }), this._stage.canvas);
  }

  _load() {
    const resources = [
      { id: 'bg', src: 'https://image.baidu.com/search/detail?ct=503316480&z=0&tn=baiduimagedetail&ipn=d&word=%E5%A3%81%E7%BA%B8&step_word=&ie=utf-8&in=&cl=2&lm=-1&st=-1&hd=0&latest=0&copyright=0&cs=1682390410,2278934715&os=222139299,3248330440&simid=3558823697,544423613&pn=2&rn=1&di=38060&ln=30&fr=&fmq=1567133149621_R&fm=&ic=0&s=undefined&se=&sme=&tab=0&width=&height=&face=undefined&is=0,0&istype=2&ist=&jit=&bdtype=0&spn=0&pi=0&gsm=0&objurl=http%3A%2F%2Fc.hiphotos.baidu.com%2Fzhidao%2Fpic%2Fitem%2F3ac79f3df8dcd100501e1ae0708b4710b8122f75.jpg' },
  ];

  this.queue = new Hilo.LoadQueue();
  this.queue.add(resources);
  this.queue.on('complete', this._initBackground.bind(this));
  this.queue.start();
  }

  _initBackground() {
    const ground = new Hilo.Bitmap({
        image: '/static/ground.png',
    }).addTo(this._stage);

    ground.height = 281;
    ground.width = 1536;
    ground.y = this.stageHeight - 100;
    console.log(ground, ground.height);
    // 循环移动地面
    const tweenh = Hilo.Tween.to(ground, { x: -60 }, { duration: 300, loop: true });
    this._ticker.addTick(Hilo.Tween);
    // tweenh.pause();
  }

   _onStart() {
   }

 _loadResources() {
      let resource = null;
      const loader = new XMLHttpRequest();

      const loadHandler = () => {
          if (!resource) {
              return;
          }
          console.log(resource, '234532343234');
          // console.log(loader.response, '1234');
          if (Array.isArray(loader.response)) {
              this._hiloResources[resource] = loader.response;
          } else if (resource.indexOf('.json') > 0) {
            // console.log(loader.response, '4444');
              this._hiloResources[resource] = JSON.parse(loader.response);
          }

         this._initBackground();

          nextRemote();
      };

      const nextRemote = () => {
          if (this._resources.length === 0) {
              this._onStart();
              this._ticker.start(false);
              return;
          }

          resource = this._resources.pop();
          if (resource) {
              loader.open('GET', resource, true);

              if (resource.indexOf('.dbbin') > 0) {
                  loader.responseType = 'arraybuffer';
                  loader.send();
              } else if (resource.indexOf('.json') > 0) {
                  loader.responseType = 'text';
                  loader.send();
              } else if (resource.indexOf('.png') > 0) {
                  const imageElement = document.createElement('img');
                  imageElement.src = resource;
                  imageElement.onload = () => {
                      nextRemote();
                  };
                  this._hiloResources[resource] = imageElement;
              }
          } else {
              nextRemote();
          }
      };

      loader.addEventListener('loadend', loadHandler);
      nextRemote();
  }

   tick() {
  }

  get stageWidth() {
      return (this._stage.viewport).width;
  }

  get stageHeight() {
      return (this._stage.viewport).height;
  }
}
